"use client";

import React from "react";
import { GoogleAuthProvider, signInWithPopup } from "firebase/auth";
import { auth } from "@/firebaseConfig"; // make sure the path is correct
import { useRouter } from "next/navigation";

export default function LoginPage() {
  const router = useRouter();

  const signInWithGoogle = async () => {
    const provider = new GoogleAuthProvider();
    provider.setCustomParameters({ prompt: "select_account" });

    try {
      const result = await signInWithPopup(auth, provider);
      const user = result.user;

      console.log("✅ Logged in as:", user.displayName);
      localStorage.setItem("user", JSON.stringify({
        name: user.displayName,
        email: user.email,
        photo: user.photoURL,
      }));

      // ✅ Route to /homepage after successful login
      router.push("/homepage");
    } catch (error) {
      console.error("❌ Login failed:", error);
    }
  };

  return (
    <div className="min-h-screen flex flex-col items-center justify-center bg-black text-white px-4">
      <h1 className="text-4xl font-bold mb-8">Login to Cinephile</h1>
      <button
        onClick={signInWithGoogle}
        className="bg-white text-black px-6 py-3 rounded-full hover:scale-105 transition"
      >
        Sign in with Google
      </button>
    </div>
  );
}
